.hero {
    text-align: center;
    padding: 100px 20px;
    background: #61dafb;
    color: white;
  }
  
  .hero h1 {
    font-size: 3em;
    margin-bottom: 20px;
  }
  
  .hero p {
    font-size: 1.2em;
    margin-bottom: 20px;
  }
  
  .hero button {
    padding: 15px 30px;
    font-size: 1em;
    cursor: pointer;
    background-color: white;
    color: #61dafb;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  .hero button:hover {
    background-color: #e0f7ff;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .hero h1 {
      font-size: 2em;
    }
  
    .hero p {
      font-size: 1em;
      margin-bottom: 10px;
    }
  
    .hero button {
      padding: 12px 24px;
      font-size: 0.9em;
    }
  }
  