#testimonials {
    padding: 50px 20px;
    background: #ffffff;
    text-align: center;
  }
  
  .testimonials {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
  }
  
  .testimonial {
    max-width: 600px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .testimonial p {
    font-style: italic;
    color: #555;
  }
  
  .testimonial h4 {
    margin-top: 10px;
    color: #282c34;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .testimonial {
      width: 100%;
    }
  }
  