.batch-section {
  padding: 60px 0;
  background-color: #f7f7f7;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 20px;
}

.section-title {
  text-align: center;
  margin-bottom: 50px;
  font-size: 2.5em;
  font-weight: bold;
  color: #333;
  position: relative;
}

.section-title::after {
  content: '';
  display: block;
  width: 80px;
  height: 4px;
  background: #007BFF;
  margin: 20px auto 0;
}

.batch-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.batch-item {
  flex: 1;
  min-width: calc(50% - 20px);
  margin: 10px;
  padding: 30px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
}

.batch-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
}

.batch-item h3 {
  font-size: 1.5em;
  margin-bottom: 15px;
  color: #007BFF;
}

.batch-item p {
  font-size: 1.1em;
  line-height: 1.6;
  margin-bottom: 15px;
  color: #555;
}

.batch-item strong {
  color: #333;
}

.enroll-button {
  display: inline-block;
  padding: 12px 25px;
  font-size: 1em;
  color: #fff;
  background-color: #007BFF;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.enroll-button:hover {
  background-color: #0056b3;
}

@media (max-width: 768px) {
  .batch-list {
    flex-direction: column;
    align-items: center;
  }
  .batch-item {
    min-width: 100%;
    margin: 10px 0;
  }
}
