.App {
  font-family: Arial, sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  transition: background-color 0.3s, color 0.3s;
}

/* General Responsive Design */
@media (max-width: 768px) {
  body {
    padding: 0;
    margin: 0;
  }
}
