#features {
    padding: 50px 20px;
    background-color: #f9f9f9;
    text-align: center;
  }
  
  .features {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }
  
  .feature {
    flex: 1;
    max-width: 300px;
    /* padding: 20px; */
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: white;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .feature h3 {
    margin-bottom: 10px;
    color: #282c34;
  }
  
  .feature p {
    color: #555;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .features {
      flex-direction: column;
      align-items: center;
    }
  
    .feature {
      max-width: none;
      width: 100%;
    }
  }
  