/* Header.css */

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px; /* Adjusted padding to reduce height */
  background: linear-gradient(135deg, #6e8efb, #a777e3); /* Gradient background */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  height: 60px; /* Set a fixed height */
}

.logo {
  font-size: 1.3em; /* Reduced font size */
  font-weight: bold;
  color: #fff; /* Text color for logo */
}

nav {
  display: flex;
  align-items: center;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

ul li {
  margin-right: 20px;
}

ul li:last-child {
  margin-right: 0;
}

ul li a {
  text-decoration: none;
  color: #fff; /* Text color for links */
  transition: color 0.3s ease;
}

ul li a:hover {
  color: #f0f0f0; /* Hover color for links */
}

/* Hamburger Menu */

.hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
  padding: 5px;
}

.line {
  width: 25px;
  height: 3px;
  background-color: #fff; /* Color of hamburger lines */
  margin: 3px 0;
  transition: transform 0.3s ease;
}

.open .line:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}

.open .line:nth-child(2) {
  opacity: 0;
}

.open .line:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}

@media (max-width: 768px) {
  .logo {
    font-size: 1.1em; /* Adjusted font size for smaller screens */
  }

  .hamburger {
    display: flex;
  }

  nav {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: #fff;
    background: linear-gradient(135deg, #6e8efb, #a777e3); /* Gradient background */
    z-index: 10;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: left 0.3s ease;
  }

  nav.open {
    left: 0;
  }

  ul {
    flex-direction: column;
    text-align: center;
    margin-top: 50px;
  }

  ul li {
    margin: 20px 0;
  }

  ul li:last-child {
    margin-bottom: 0;
  }

  ul li a {
    font-size: 1.2em;
  }
}
